import CircularProgress from '@material-ui/core/CircularProgress';
import React, { FC, useState } from 'react';
import { Title } from '../../components/Title/Title';
import style from './Gallery.module.css';

const imagesPath = process.env.REACT_APP_PHOTO_URL as string;
const thumbnailsPath = `${imagesPath}/thumbnails`;

type TImageProps = {
  url: string;
  title: string;
};

const Image: FC<TImageProps> = ({ url, title }) => {
  const [loaded, setLoaded] = useState(false);
  return (
    <>
      <img
        src={url}
        alt={title}
        className={loaded ? `${style.Image} ${style.Loaded}` : `${style.Image}`}
        onLoad={() => {
          setLoaded(true);
        }}
      />

      {!loaded && <CircularProgress className={style.Spinner} />}
    </>
  );
};

export const Gallery: FC = () => {
  const showCarousel = (index: number): void => {
    console.log(index);
  };

  const thumbnails = [...new Array(23)].map((_, index) => ({
    url: `${thumbnailsPath}/img${index + 1}.jpeg`,
    title: `thumbnail-${index + 1}`,
  }));

  const gallery = thumbnails.map((thumbnail, index) => (
    <div
      key={`thumbnail-${index}`}
      className={style.Box}
      onClick={(event) => {
        showCarousel(index);
      }}
    >
      <Image {...thumbnail} />
    </div>
  ));

  return (
    <>
      <Title title="Gallery" />
      <div className={style.GalleryWall}>{gallery}</div>
    </>
  );
};
