import React from 'react';

import { useParams } from 'react-router-dom';
import { SideBar } from '../../components/SideBar/SideBar';
import { PageSvc } from '../../services/PageSvc';
import { Title } from '../../components/Title/Title';
import { Content } from '../../components/Content/Content';

import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

interface IParams {
  category: string;
  subPage: string;
}
const useStyle = makeStyles((theme) => {
  const colorSecondary = theme.palette.text.secondary;
  return {
    container: {},

    card: {
      display: 'flex',
      width: '100%',
      '& > *': {
        flex: 1,
      },
    },
    Paragraph: {
      textIndent: 30,
      padding: 30,
      color: colorSecondary,
    },
  };
});
export const SubPage = () => {
  const classes = useStyle();
  const { category, subPage } = useParams<IParams>();
  const sideBarLinks = PageSvc.getSideBarLinks(category);
  const page = PageSvc.getSubPage(category, subPage);

  return (
    <Grid
      item
      container
      className={classes.container}
      justify="center"
      alignContent="flex-start"
    >
      <Grid item container xs={12}>
        <Title title={category} subTitle={page?.title} />
      </Grid>

      <Content>
        {sideBarLinks && (
          <Grid item container sm={4} justify="center">
            <SideBar sideBarLinks={sideBarLinks} />
          </Grid>
        )}

        <Grid item container sm={8}>
          <Grid item container>
            <Typography>
              <div
                className={classes.Paragraph}
                dangerouslySetInnerHTML={{ __html: page?.content || '' }}
              />
            </Typography>
          </Grid>
        </Grid>
      </Content>
    </Grid>
  );
};
