import React, { FC, useEffect, useState } from 'react';

import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';

const useStyle = makeStyles((theme) => {
  const bgTitleFirstColor = theme.palette.secondary.dark;
  const bgTitleFirstLighterColor = 'rgba(197, 17, 98,0.5)';
  const bgTitleSecondColor = 'transparent';

  const fontFamily = "'Big Shoulders Stencil Display', cursive";
  const textShadow = `2px 7px 5px rgba(0,0,0,0.3), 
                      0px -4px 10px rgba(255,255,255,0.3)`;
  return {
    Container: { flex: 1, marginTop: 64 },
    TitleContainer: {
      backgroundImage: () =>
        `linear-gradient(45deg,${bgTitleFirstLighterColor},${bgTitleFirstColor} 10%,${bgTitleFirstColor} 15%,${bgTitleFirstLighterColor} 25%, ${bgTitleFirstColor} 30%,${bgTitleFirstColor} 45%, ${bgTitleFirstLighterColor}47%,${bgTitleFirstColor} 48%, ${bgTitleFirstColor} 60%, ${bgTitleSecondColor} 60.5%)`,
      backgroundPosition: (props: { reTitle: boolean }) =>
        props.reTitle ? '100% 0' : '0% 0',
      backgroundSize: '250%',
      transition: 'background-position 0.5s',
      height: 150,
      boxShadow:
        '0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12)',
    },
    Title: {
      color: '#fff',
      fontFamily: fontFamily,
      textShadow: textShadow,
    },
    SubTitle: {
      color: '#fff',
      textShadow: textShadow,
      fontFamily: fontFamily,
    },
    WhitSub: {
      color: '#fff',
      fontFamily: fontFamily,
      textShadow: `2px 7px 5px rgba(0,0,0,0.5), 
      0px -4px 10px rgba(255,255,255,0.3)`,
    },
  };
});

type TitleProps = {
  title: string;
  subTitle?: string;
};
export const Title: FC<TitleProps> = ({ title, subTitle }) => {
  const theme = useTheme();
  const downXsQuery = useMediaQuery(theme.breakpoints.down('xs'));
  const [titles, setTitles] = useState<TitleProps>({ title: '' });
  const [reTitle, setReTitle] = useState(false);
  const classes = useStyle({ reTitle });

  useEffect(() => {
    setReTitle(true);
    const timer = setTimeout(() => {
      setTitles({ title, subTitle });
      setReTitle(false);
    }, 500);
    return () => {
      clearTimeout(timer);
      setTitles({ title, subTitle });
    };
  }, [title, subTitle]);

  const content = titles.subTitle ? (
    <>
      <Typography variant="h2" component="h2" className={classes.SubTitle}>
        {titles.subTitle}
      </Typography>
      <Typography variant="h3" component="h1" className={classes.WhitSub}>
        {titles.title}
      </Typography>
    </>
  ) : (
    <>
      <Typography variant="h2" component="h1" className={classes.Title}>
        {titles.title}
      </Typography>
    </>
  );

  return (
    <Grid sm={11} item container className={classes.TitleContainer}>
      {!downXsQuery && <Grid sm={2} />}
      <Grid
        xs={12}
        sm={8}
        justify="space-between"
        alignItems="center"
        item
        container
      >
        {content}
      </Grid>
      {!downXsQuery && <Grid sm={2}></Grid>}
    </Grid>
  );
};
